body {
	background-color: $section-bg-dark;
	line-height: 1.4;
	font-size: 18px;
	@include roboto-reg;
	color: #fff;
	&.is-hidden {
		overflow: hidden; } }

a {
	text-decoration: none;
	color: inherit;
	@include roboto-reg;
	@include trans; }

//title
.title-primary {
	@include roboto-light;
	font-size: 72px;
	@include css-lock(40, 72, 900, 1920);
	line-height: 1;
	&_centered {
		text-align: center; } }
.title-secondary {
	line-height: 1;
	@include roboto-med;
	font-size: 48px;
	@include css-lock(32, 48, 900, 1920); }

//icons
.icon-check {
	font-size: 32px;
	fill: #fff; }
.icon-close {
	font-size: 15px;
	fill: #0ca8fd; }

[class^="i-icon"] {
	display: inline-block;
	vertical-align: middle; }
.i-icon-money {
	@include s(money); }
.i-icon-api {
	@include s(api); }
.i-icon-card {
	@include s(card); }
.i-icon-graph {
	@include s(graph); }
.i-icon-guard {
	@include s(guard); }
.i-icon-help {
	@include s(help); }
.i-icon-link {
	@include s(link); }
.i-icon-profile {
	@include s(profile); }
.i-icon-sale {
	@include s(sale); }
.i-icon-worker {
	@include s(worker); }
.i-icon-discount {
	@include s(discount); }
.i-icon-help-new {
	@include s(help-new); }

.i-icon-ruble {
	@include s(ruble); }

//flags
.i-icon-ru {
	@include s(ru); }
.i-icon-sw {
	@include s(sw); }
.i-icon-rum {
	@include s(rum); }
.i-icon-uk {
	@include s(uk); }
